<template>
    <div class="design page-container">
      <div v-for="(v, i) in DESIGN_CONTENTS" :key="i" class="info-paragraph">
        <h2>{{ v.title }}</h2>
        <template v-for="(item, index) in v.paragraphs">
          
          <div :key="index + 'i'" v-if="item.type && item.type == 'img'" class="info-img" :class="item.class" :style="{ backgroundImage: `url(${getImg(item.url)})` }">
          </div>
          <p class="info-text" :key="index + 'p'" v-else>{{ item }}</p>
        </template>
      </div>
    </div>
  </template>
      
      <script>
  import { DESIGN_CONTENTS } from "@/constant";
  export default {
    name: "DesignPage",
    components: {},
    data() {
      return {
        DESIGN_CONTENTS,
      };
    },
    computed: {
      getImg() {
        return function (url) {
          return require(`@/assets/${url}`);
        };
      },
    },
    methods: {},
  };
  </script>
      
      <style>
  @import "../style/design.scss";
  </style>
      